import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DialogData } from 'src/app/shared/commmon/dialogue/dialogue.component';
import { MediaDialogData } from 'src/app/shared/commmon/ServiceClass';

@Component({
  selector: 'app-view-media',
  templateUrl: './view-media.component.html',
  styleUrls: ['./view-media.component.scss']
})
export class ViewMediaComponent implements OnInit {
  isPlaying: any;
HideVideo = true;
HideImage = true;
ImageSource = '';
VideoSource = '';
  ngOnInit() {
  }
  constructor(public dialogRef: MatDialogRef<ViewMediaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: MediaDialogData) {
      if (data.MediaType === 1) {
        this.HideImage = false;
        this.HideVideo = true;
        this.ImageSource = data.MediaSource;
      } else {
        this.HideImage = true;
        this.HideVideo = false;
        this.VideoSource = data.MediaSource;
      }
    }

  onNoClick(): void {
    this.dialogRef.close();
  }


}
